.site-footer {
    background-color: $mainBlue;
    color: #fff;
    padding: .5rem 0;
    text-align: center;

    &__text {
        display: block;
        margin-bottom: 1rem;
        font-weight: 200;

        @include respond(atMedium) {
            display: inline;
            margin-bottom: 0;
            margin-right: 1.4rem;
        }
    }
}